.user-data {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 40px;

    color: #111;
    font-family: 'Merriweather';
    font-size: 20px;
    line-height: 28px;

    &__row {
        display: flex;
        flex-direction: row;
        gap: 10px;
    }

    &__row-label {
        font-weight: bold;
    }
}
